.container-fluid{
    margin-top: 20px;
  }

jumbotron, h1, p{
    padding: 10px
}

.carousel-inner{
    width:100%;
    max-height: 400px !important;
  }

#footer{
    border:1px solid white;
   margin:0 auto;
    padding:10px;

}

.opbox{
    margin:-5px;
}
.card{
    border:1px solid black;
    padding:10px;
    margin :30px !important;
}
#basic-nav-dropdown{
    color:white;
}

#topslip{
    border:1px solid black;
    margin:10px;
    padding: 5px;
}

.accordion img{
    width:50%;
    padding:5px;
}

@media(min-width:1367px) and (max-width:1920px){
    #tiles{
        width:450px
    }
    #paradise{
        width:750px;
        height:380px;
        padding:20px;
    }
    .jumbo {
        height:600px;
        width:1250px;
    }
    #logo{
        margin-left:330px;
        width:65px;
        height:40px;
    }
    #jumb {
        background-image: url("../static/PollachiEco.jpg");
        margin-left: 100px;
         height:600px;
         width:1250px;
      }
}

@media (min-width:768px) and (max-width:1366px){
    #tiles{
        width:450px
    }
    #paradise{
        width:600px;
        height:380px;
        padding:10px;
    }
    .jumbo {
        height:600px;
        width:1250px;
    }
    #jumb{
        background-image: url("../static/PollachiEco.jpg");
        margin-left: 30px;
        height:600px;
        width:95%;

      }
      #logo{
          margin-left:250px;
      }
      #navi img{
          align-items: center;
      }

}
@media(max-width:767px){
    #tiles{
        width:450px;
    }
    #paradise{
        width:400px;
        height:260px;
        padding:5px;
    }
    #jumb{
        background-image: url("../static/PollachiEco.jpg");
        height:500px;
        width:100%;
        margin-left:5px;
    }
    .opbox{
        margin:-25px;
    }
    .acc{
        margin:-20px;
    }
}



